import React from 'react';
import { Container } from "../../globalStyles";
import { FaLinkedin, FaEnvelope, FaYoutube } from 'react-icons/fa';
import { ContactContainer } from './styles';

const Contact = () => {
  return (
    <Container id="contact">
      <ContactContainer>
        <h2 style={styles.center}>Contact</h2>
        <ul>
          <li>
            <a
              href="https://www.linkedin.com/in/rahuls360/"
              target="_blank"
              rel="noopener noreferrer"
            >
              <FaLinkedin />
            </a>
          </li>
          <li>
            <a
              href="mailto:rahuls360@gmail.com"
              target="_blank"
            >
              <FaEnvelope />
            </a>
          </li>
          <li>
            <a
              href="https://www.youtube.com/channel/UCVsDcEX6kB9ZNAWdlxrnn0A"
              target="_blank"
              rel="noopener noreferrer"
            >
              <FaYoutube />
            </a>
          </li>
        </ul>
      </ContactContainer>
    </Container>
  );
}

const styles = {
  center: {
    textAlign: 'center'
  }
}

export default Contact;